<template>
    <div class="page-content">
        <div class="page-block mb30 pay-block">
            <div class="title">Ваш баланс <span>2345р.</span></div>
            <div class="pay-block__form">
                <div class="input-wrap-short">
                    <label>Новый платёж</label>
                    <input type="text" placeholder="Введите сумму" v-model="amount">
                </div>
                <div class="label">Выберите платёжную систему</div>
                <div class="flex-start pay-block__items">
                    <div :class="[
                            {'pay-block__item-active': item.is_active},
                            'pay-block__item'
                        ]"
                         @click="setActive(i)" v-for="(item, i) in pay_systems"
                         :key="i">
                        <img :src="item.img" alt="">
                    </div>
                </div>
                <button class="button-gradient">Применить</button>
            </div>
        </div>

        <div class="news-block-wrapper page-block page-padding-y">
            <h1>Свежие новости</h1>
            <div class="news-block">
                <div v-for="article in news" :key="article.id" class="news-item flex-between align-top">
                    <div class="text">
                        <h3>{{article.title}}</h3>
                        <p>{{article.text}}</p>
                    </div>
                    <div class="image">
                        <img :src="genImageUrl(article)" alt="">
                    </div>
                </div>
            </div>
            <div class="load-more">Ещё новости</div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "balance",
        props: ['news'],
        data: () => ({
            amount: null,
            pay_systems: [
                {
                    img: '/img/payment/1.png',
                    is_active: true
                },
                {
                    img: '/img/payment/2.png',
                    is_active: false
                },
                {
                    img: '/img/payment/3.png',
                    is_active: false
                }
            ]
        }),
        methods: {
            genImageUrl(article) {
                return `${process.env.VUE_APP_SERVER_URL}${article.image}`
            },
            setActive(index) {
                this.pay_systems.forEach(item => item.is_active = false)
                this.pay_systems[index].is_active = true
            }
        }
    }
</script>

